import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';

class Slideshow extends React.Component{
constructor(props){
   super(props);
   this.state={mercedes:1};
 }
  auto=()=>{
    this.timer=setInterval(()=>{
      this.setState({mercedes:this.state.mercedes===7?1:this.state.mercedes+1})
    },1000)
   }

 stop=()=> {
  clearInterval(this.timer);
}
  next=()=>{
    this.setState({mercedes:this.state.mercedes===7?7:this.state.mercedes+1})
  }
  prev=()=>{
    this.setState({mercedes:this.state.mercedes===1?1:this.state.mercedes -1})
   }
  render(){
     return(
      <div onMouseEnter={clearInterval} className="putcenter">
        <button type="button" onClick={this.prev} className="back"><i className="fa fa-chevron-circle-left" aria-hidden="true"></i></button>
         <button type="button" onClick={this.next} className="next"><i className="fa fa-chevron-circle-right" aria-hidden="true"></i></button>
         <img src={"images/mercedes"+this.state.mercedes+".png"} alt="anh 1" className="slide-img" /><br/>     
        <button type="button" onClick={this.auto} className="auto">Au<i className="fab fa-autoprefixer"></i>To</button>    
          <button type="button" onClick={this.stop} className="stop">Stop <i className="fab fa-autoprefixer"></i></button>     
      </div>
     )
  }
    
 }
ReactDOM.render(
  <React.StrictMode>
  <Slideshow></Slideshow>
  </React.StrictMode>, 
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
